import React from "react";
import "./styles.css";
import { useState } from "react";

const foodDB = {
  vidarbhian: [
    { name: " tarri poha", taste: "tangy and spicy" },
    { name: "patodi rassa", taste: "spicy" }
  ],

  khandeshi: [
    {
      name: "shev bhaji",
      taste: "spicy"
    },
    {
      name: "vangyache bharit",
      taste: "tangy"
    }
  ],
  konkani: [
    {
      name: "alu vadi",
      taste: "sour"
    },
    {
      name: "fish curry",
      taste: "spicy"
    }
  ]
};

export default function App() {
  const [selectedcuisine, setcuisine] = useState("konkani");
  function cuisineClickHandler(cuisine) {
    setcuisine(cuisine);
  }
  return (
    <div className="App">
      <h1> üçó goodfoods </h1>
      <p style={{ fontSize: "smaller" }}>
        {" "}
        Checkout my favorite foods. Select a cuisine to get started{" "}
      </p>

      <div>
        {Object.keys(foodDB).map((cuisine) => (
          <button
            onClick={() => cuisineClickHandler(cuisine)}
            style={{
              cursor: "pointer",
              background: "#E5E7EB",
              borderRadius: "0.5rem",
              padding: "0.5rem  1rem",
              border: "1px solid black",
              margin: "1rem 0.3rem"
            }}
          >
            {cuisine}
          </button>
        ))}
      </div>
      <hr />
      <div style={{ textAlign: "left" }}>
        <ul style={{ paddingInlineStart: "0" }}>
          {foodDB[selectedcuisine].map((foods) => (
            <li
              key={foods.name}
              style={{
                listStyle: "none",
                padding: "1rem",
                border: "1px solid #D1D5DB",
                width: "70%",
                margin: "1rem 0rem",
                borderRadius: "0.5rem"
              }}
            >
              {" "}
              <div style={{ fontSize: "larger" }}> {foods.name} </div>
              <div style={{ fontSize: "smaller" }}> {foods.taste} </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
